@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:'Sora', sans-serif;display:flex;flex-direction:row;background-color:#a0a6f1;min-height:100vh}@media (max-width: 640px){.App{display:flex;flex-direction:column;width:100%;overflow-x:hidden}}.App .editor-wrapper{color:black;width:50vw;padding:25px}@media (max-width: 640px){.App .editor-wrapper{min-width:85vw;height:50vw}}.App .editor-wrapper #editor{font-family:'Sora', sans-serif;padding:5px;resize:none;width:100%;height:100%;border-radius:5px}.App .preview-wrapper{color:black;width:50vw;padding:25px}@media (max-width: 640px){.App .preview-wrapper{min-width:93%;padding:10px;margin-left:1vw}}

.markdown{padding:0 1rem}.markdown blockquote{border-left:3px solid #052727;color:#052727;padding-left:5px;margin-left:25px}.markdown>p>code{padding:3px}.markdown code{background:white;font-size:0.875rem;font-weight:bold;border-radius:5px}.markdown pre{display:block;overflow:auto;background:white;padding:5px;line-height:1.2;border-radius:5px}.markdown h1{border-bottom:2px solid #052727}.markdown h2{border-bottom:1px solid #052727}.markdown table{border-collapse:collapse}.markdown td,.markdown th{border:2px solid #052727;padding-left:5px;padding-right:5px}.markdown img{padding-left:20px;display:block;width:10%;height:4vw}@media (max-width: 640px){.markdown img{height:8vw}}

